import React, { useState, useEffect } from "react";

import {
  ListItem,
  ListItemIcon,
  Collapse,
  List,
  ListItemText,
} from "@material-ui/core";
import { AddBox, IndeterminateCheckBox } from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";

import styles from "assets/jss/material-dashboard-pro-react/views/elementryManagerPageStyle";

const useStyle = makeStyles(styles);

export default function FileManagerListItem(props) {
  const [open, setOpen] = useState(true);
  const classes = useStyle();

  useEffect(() => {
    setOpen(props.collapsedState);
  }, []);

  const toggleCollapse = (event) => {
    event.stopPropagation();
    setOpen(!open);
  };

  const handleClick = (event, value) => {
    event.stopPropagation();
    props.setCurrentCategory({ ...value });
    props.setStateSelected(value.id);
    props.setCollapsedState(true);
  };

  return (
    <div>
      <ListItem
        button
        onClick={(ev) => {
          if (props.__this.hasFile) {
            handleClick(ev, props.__this);
          }
        }}
        selected={props.__this.id === props.selected}
        className={props.__this.hasFile ? classes.hasFile : classes.noFile}
      >
        <span
          className={classes.emptySpace}
          style={{ width: props.__this.depth * 20 + "px" }}
        ></span>
        {props.__this.child ? (
          <div className={classes.listRow}>
            <ListItemIcon style={{ zIndex: 100 }}>
              {open ? (
                <IndeterminateCheckBox
                  onClick={(ev) => toggleCollapse(ev)}
                ></IndeterminateCheckBox>
              ) : (
                <AddBox onClick={(ev) => toggleCollapse(ev)}></AddBox>
              )}
            </ListItemIcon>
            <ListItemText primary={props.__this.name}></ListItemText>
          </div>
        ) : (
          <div className={classes.listRow}>
            <ListItemText primary={props.__this.name}></ListItemText>
          </div>
        )}
      </ListItem>
      {props.__this.child ? (
        <Collapse in={open}>
          <List component="div" disablePadding>
            {props.__this.children.map((val) => {
              return (
                <FileManagerListItem
                  key={val.id}
                  __this={val}
                  categories={
                    props.__this.children ? props.__this.children : []
                  }
                  category={props.category}
                  setCurrentCategory={props.setCurrentCategory}
                  collapsedState={props.collapsedState}
                  setCollapsedState={props.setCollapsedState}
                  setStateSelected={props.setStateSelected}
                  selected={props.selected}
                />
              );
            })}
          </List>
        </Collapse>
      ) : null}
    </div>
  );
}
