import React, { useState, useEffect } from "react";
import Axios from 'axios';
import CategoryListItem from './CategoryListItem.js';

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import List from "@material-ui/core/List";
import Select from "@material-ui/core/Select";

import Dialog from "@material-ui/core/Dialog";
import { TextField, DialogActions } from "@material-ui/core";
import DialogContent from "@material-ui/core/DialogContent";
import CategoryContents from "views/Components/CategoryContents";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";

import styles from "assets/jss/material-dashboard-pro-react/views/elementryManagerPageStyle.js";
import modalStyle from "assets/jss/material-dashboard-pro-react/modalStyle.js";

const useStyles = makeStyles(styles);
const useModalStyles = makeStyles(modalStyle);

export default function Manager(props) {
  const [currentCategory, setCurrentCategory] = useState({});
  const [categories, setCategories] = useState([]);
  const [collapsedState, setCollapsedState] = useState(false);
  const [openAddingModal, toggleModalofAdding] = useState(false);
  const [openEdittingModal, toggleModalofEditting] = useState(false);
  const [selected, setStateSelected] = useState(1001);
  const [availableTypes, setAvailableTypes] = useState([]);

  useEffect(() => {
    getAllCategories();
  }, []);

  const getAllCategories = () => {
    let prevState = currentCategory.id
    if (!prevState) {
      prevState = props.classification === 'E' ? 1001 : 1002;
    } 

    Axios.get(`/api/v1/category/get/root`, {
      params: {
        type: props.classification
      }
    })
      .then((val) => {
        setCategories(val.data.data);

        // to remember a previous status
        Axios.get('/api/v1/category/get/listbyid', {
          params: {
            'category-id': prevState
          }
        })
        .then((q) => {
          if (q.data.succeeded) {
            setCurrentCategory(q.data.data[0]);
          }          
        })
        
      })
      .catch((error) => {
        if (error.response.data.status === 403 && error.response.data.code === 'C006') {
          window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');
          props.history ? props.history.push('/login') : window.location.href = '/login';
        }
      })
  }
  
  const verifyTypes = (id) => {
    Axios.get('/api/v1/category/get/accept-type', {
      params: {
        'category-id': id
      }
    })
      .then((res) => {
        const types = res.data.data[0];
        let availabletype = []
        if (types.category === 'true') {
          availabletype.push('category')
        }
        if (types.side === 'true') {
          availabletype.push('side')
        }
        if (types.top === 'true') {
          availabletype.push('top')
        }

        setAvailableTypes(
          availabletype.map((val, index) => {
            return (
              <option key={index} value={val}>{val}</option>
            )
          })
        )
      })
  }

  const addCategory = (event) => {
    const inputValue = document.getElementById("categoryName").value;
    const inputType = document.getElementById('categoryType').value;
    const inputValue_replaced = inputValue.trim().replace('/\s{2}/g', ' ');
    
    if (typeof inputType !== 'string' || inputType.length === 0) {
      window.alert('Type을 선택해 주세요');
      throw 'input type is empty';
    }

    if (typeof inputValue === 'string' && inputValue_replaced.length !== 0) {
      const dataset = {
        'name': inputValue_replaced,
        'order': 0,
        'parentId': currentCategory ? currentCategory.id : '1000',
        'use': true,
        'type': inputType.toUpperCase()
      };
  
      implementCreateCategory(dataset);
      toggleModalofAdding(false);
    } else {
      window.alert('카테고리 이름을 정확하게 입력해주세요.');
      throw 'input string is empty';
    }
    
  };

  const implementCreateCategory = (params) => {
    Axios.get('/api/v1/category/create', {
      params: {
        'is-use': params.use,
        'name': params.name,
        'order': params.order,
        'parent-id': params.parentId,
        'type': params.type
      }
    })
      .then((val) => {
        if (val.data.succeeded) {
          // update DOM to be new
          getAllCategories();
        }
      })
      .catch((error) => {
        if (error.response.data.status === 403 && error.response.data.code === 'C006') {
          window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');
          props.history ? props.history.push('/login') : window.location.href = '/login';
        }
      })
  }

  const editCategory = (event, index) => {
    const inputValue = document.getElementById("ReplacedText").value;
    const inputValue_replaced = inputValue.trim().replace('/\s{2}/g', ' ');

    if (inputValue_replaced.length === 0) {
      inputValue = '';
      return false;
    } else {
      const categoryId = currentCategory.id;
      implementEditCategory(categoryId, {name: inputValue_replaced})
    }
  };

  const implementEditCategory = (category_id, params) => {
    if (typeof params !== 'object') {
      throw 'parameters is not a object, you must set a parameter with object type.';
    } else if (typeof category_id !== 'string' && category_id === '') {
      throw 'category_id should be set with String type or you should check it again if category_id has been set as empty string'
    } else {
      Axios.get('/api/v1/category/update', {
        params: {
          'category-id': category_id,
          'is-use': params.use? params.use : currentCategory.use,
          'name': params.name? params.name : currentCategory.name,
          'order': params.order? params.order: currentCategory.order,
          'parent-id': params.parent_id? params.parent_id : currentCategory.parent_id,
          'type': params.type? params.type: currentCategory.type
        }
      })
        .then((val) => {
          if (val.data.succeeded) {
            getAllCategories();
            toggleModalofEditting(false);
          }
        })
        .catch((error) => {
          if (error.response.data.status === 403 && error.response.data.code === 'C006') {
            window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');
            props.history ? props.history.push('/login') : window.location.href = '/login';
          }
        })
    }
  }

  const implementDeleteCategory = (id) => {
    if (currentCategory.use) {
      window.alert("사용중인 카테고리는 삭제할 수 없습니다.");
      return false;
    } else {
      const confirm = window.confirm(
        "정말 " + currentCategory.name + " 을 삭제 하시겠습니까?"
      );
      if (confirm) {
        Axios.get('/api/v1/category/delete', {
          params: {
            'category-id': id
          }
        })
          .then((val) => {
            if (val.data.succeeded) {
              getAllCategories();
            }
          })
          .catch((error) => {
            if (error.response.data.status === 403 && error.response.data.code === 'C006') {
              window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');
              props.history ? props.history.push('/login') : window.location.href = '/login';
            }
          })
      }
    }
  };

  const toggleModal_AddingCategory = () => {
    toggleModalofAdding(!openAddingModal);
  };
  const toggleModal_EdittingCategory = () => {
    toggleModalofEditting(!openEdittingModal);
  };

  const classes = useStyles();
  const modalClasses = useModalStyles();

  return (
    <div className={classes.container}>
      <GridContainer justify="flex-start">
        <GridItem xs={4} sm={12} md={4}>
          <Paper elevation={0} variant={"outlined"}>
            <div className={classes.paperContents}>
              <div className={classes.categoriesContainer}>
                <List>
                  {
                    categories.map((val) => {
                      return (
                        <CategoryListItem
                          key={val.id}
                          __this={val}
                          categories={categories}
                          category={currentCategory}
                          setCurrentCategory={setCurrentCategory}
                          collapsedState={collapsedState}
                          setCollapsedState={setCollapsedState}
                          setStateSelected={setStateSelected}
                          selected={selected}
                          verifyTypes={verifyTypes}
                        />
                      )
                    })
                  }
                  
                </List>
              </div>
              <div className={classes.collectionBottons}>
                <Button
                  color="facebook"
                  onClick={() => toggleModal_AddingCategory()}
                >
                  카테고리 추가
                </Button>
                <Button
                  color="facebook"
                  onClick={() => toggleModal_EdittingCategory()}
                >
                  이름 변경
                </Button>
                <Button
                  color="danger"
                  onClick={() => implementDeleteCategory(currentCategory.id)}
                >
                  삭제
                </Button>
              </div>
              {/* 카테고리 추가를 위한 모달 */}
              <Dialog
                open={openAddingModal}
                onClose={toggleModal_AddingCategory}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
                className={modalClasses.modal}
              >
                <DialogContent className={modalClasses.modalBody}>
                  <p className={modalClasses.modalTitle}>
                    카테고리 이름을 입력하세요.
                  </p>
                  <TextField
                    id="categoryName"
                    label=""
                    className={classes.form}
                  ></TextField>
                  <p className={modalClasses.modalTitle}>
                    카테고리 타입을 선택하세요.
                  </p>
                  <Select
                      native
                      inputProps={{
                        name: "categoryType",
                        id: "categoryType",
                      }}
                    >
                      {availableTypes}
                    </Select>
                </DialogContent>
                <DialogActions
                  className={
                    modalClasses.modalFooter +
                    " " +
                    modalClasses.modalFooterCenter
                  }
                >
                  <Button
                    color={"danger"}
                    size={"sm"}
                    onClick={() => toggleModal_AddingCategory()}
                  >
                    취소
                  </Button>
                  <Button
                    color={"facebook"}
                    size={"sm"}
                    onClick={(ev) => addCategory(ev)}
                  >
                    확인
                  </Button>
                </DialogActions>
              </Dialog>
              {/* 카테고리 이름 변경을 위한 모달 */}
              <Dialog
                open={openEdittingModal}
                onClose={toggleModal_EdittingCategory}
                className={modalClasses.modal}
              >
                <DialogContent className={modalClasses.modalBody}>
                  <p className={modalClasses.modalTitle}>
                    카테고리 이름을 입력하세요.
                  </p>
                  <TextField
                    id="ReplacedText"
                    label={currentCategory.title}
                    className={classes.form}
                  ></TextField>
                </DialogContent>
                <DialogActions
                  className={
                    modalClasses.modalFooter +
                    " " +
                    modalClasses.modalFooterCenter
                  }
                >
                  <Button
                    color={"danger"}
                    size={"sm"}
                    onClick={() => toggleModal_EdittingCategory()}
                  >
                    취소
                  </Button>
                  <Button
                    color={"facebook"}
                    size={"sm"}
                    onClick={(ev) => editCategory(ev, currentCategory.index)}
                  >
                    확인
                  </Button>
                </DialogActions>
              </Dialog>
            </div>
          </Paper>
        </GridItem>
        <GridItem xs={8} sm={12} md={8}>
          <Paper elevation={0} variant={"outlined"}>
            <div className={classes.paperContents}>
              {Object.keys(currentCategory).length === 0 &&
              currentCategory.constructor === Object ? null : (
                <CategoryContents
                  categories={categories}
                  category={currentCategory}
                  availableTypes={availableTypes}
                  reRenderCategoryLists={() => getAllCategories()}
                ></CategoryContents>
              )}
            </div>
          </Paper>
        </GridItem>
      </GridContainer>
    </div>
  );
}
