import React, { useState, useEffect } from "react";
import Axios from "axios";
import FileManagerListItem from "./FileManagerListItem.js";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import List from "@material-ui/core/List";

import CategoryContents from "views/Components/FileManagerContent";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import styles from "assets/jss/material-dashboard-pro-react/views/elementryManagerPageStyle";

const useStyles = makeStyles(styles);

export default function FileManager(props) {
  const [currentCategory, setCurrentCategory] = useState({});
  const [categories, setCategories] = useState([]);
  const [collapsedState, setCollapsedState] = useState(false);
  const [selected, setStateSelected] = useState(1001);

  useEffect(() => {
    getAllCategories();
  }, []);

  const getAllCategories = () => {
    const defaultRoot = currentCategory.id ? currentCategory.id : 1011; // default location is first content of 교과서, 지도서

    Axios.get(`/api/v1/category/get/root`, {
      params: {
        type: props.classification,
      },
    })
      .then((val) => {
        setCategories(val.data.data);

        // to remember a previous status
        Axios.get("/api/v1/category/get/file-categories", {
          params: {
            "category-id": defaultRoot,
          },
        }).then((q) => {
          if (q.data.succeeded) {
            setCurrentCategory(q.data.data[0]);
          }
        });
      })
      .catch((error) => {
        if (
          error.response.data.status === 403 &&
          error.response.data.code === "C006"
        ) {
          window.alert("세션이 만료되었습니다. 로그인 페이지로 이동합니다.");
          props.history
            ? props.history.push("/login")
            : (window.location.href = "/login");
        }
      });
  };

  const verifyTypes = (id) => {};

  const classes = useStyles();

  return (
    <div className={classes.container}>
      <GridContainer justify="flex-start">
        <GridItem xs={4} sm={12} md={4}>
          <Paper elevation={0} variant={"outlined"}>
            <div className={classes.paperContents}>
              <div className={classes.categoriesContainer}>
                <List>
                  {categories.map((val) => {
                    return (
                      <FileManagerListItem
                        key={val.id}
                        __this={val}
                        categories={categories}
                        category={currentCategory}
                        setCurrentCategory={setCurrentCategory}
                        collapsedState={collapsedState}
                        setCollapsedState={setCollapsedState}
                        setStateSelected={setStateSelected}
                        selected={selected}
                      />
                    )
                  })}
                </List>
              </div>
            </div>
          </Paper>
        </GridItem>
        <GridItem xs={8} sm={12} md={8}>
          <Paper elevation={0} variant={"outlined"}>
            <div className={classes.paperContents}>
              {Object.keys(currentCategory).length === 0 &&
              currentCategory.constructor === Object ? null : (
                <CategoryContents
                  categories={categories}
                  category={currentCategory}
                  reRenderCategoryLists={() => getAllCategories()}
                ></CategoryContents>
              )}
            </div>
          </Paper>
        </GridItem>
      </GridContainer>
    </div>
  );
}
