import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch, Redirect } from "react-router-dom";
import Axios from 'axios';

import AuthLayout from "layouts/Auth.js";
import AdminLayout from "layouts/Admin.js";
import ManagerLayout from "layouts/Manager.js";
import RegisterPage from "./views/Pages/RegisterPage.js";
import RegisterPage_M from "./views/Pages/RegisterPage_M.js";
import LoginPage from "./views/Pages/LoginPage.js";

import "assets/scss/material-dashboard-pro-react.scss?v=1.9.0";

const hist = createBrowserHistory();
Axios.defaults.baseURL = 'http://douclass-dev-test-alb-2117761333.ap-northeast-2.elb.amazonaws.com:8080';
Axios.defaults.withCredentials = true;

ReactDOM.render(
  <Router history={hist}>
    <Switch>
      <Route path="/admin" component={AdminLayout} />
      <Route path="/manager" component={ManagerLayout} />
      <Route path="/registerTextbook/element" component={RegisterPage} />
      <Route path="/registerTextbook/middle" component={RegisterPage_M} />
      <Route path="/login" component={LoginPage} />
      <Redirect from="/" to="/manager/elementry" />
    </Switch>
  </Router>,
  document.getElementById("root")
);
