import React, { useState, useEffect } from "react";
import Axios from "axios";
import cx from "classnames";
import ContentsInputs from "../Forms/ContentsInputsForms.js";

import { makeStyles } from "@material-ui/core/styles";
import styles from "assets/jss/material-dashboard-pro-react/views/RegisterPageStyle.js";
import sidebarStyles from "assets/jss/material-dashboard-pro-react/layouts/adminStyle.js";

import Sidebar from "components/Sidebar/Sidebar.js";
import AdminNavbar from "components/Navbars/AdminNavbar.js";

import routes from "routes.js";

const useStyle = makeStyles(styles);
const useStyleSidebar = makeStyles(sidebarStyles);

export default function RegisterPage(props) {
  const { ...rest } = props;
  // states and functions
  const searchQuery = new URLSearchParams(window.location.search);
  const isNew = searchQuery.get("new");
  const unitcode = searchQuery.get("seq");
  const [basicInfo, setInfos] = useState([]);
  const [flattenArray, setFlattenArray] = useState([]);

  useEffect(() => {
    changeEducationType("E");

    if (isNew !== 'true') {
      // get informations by using unitcode
      // after then, save all data into contents variable.
      Axios.get("/api/v1/book/get", {
        withCredentials: true,
        params: {
          seq: unitcode,
        },
      }).then((val) => {
        if (val.status === 200 && val.data.succeeded) {
          setInfos(val.data.data[0]);
        }
      })
    }
  }, []);

  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [miniActive, setMiniActive] = React.useState(false);
  const [image, setImage] = React.useState(require("assets/img/sidebar-2.jpg"));
  const [color, setColor] = React.useState("blue");
  const [bgColor, setBgColor] = React.useState("black");

  const classes = useStyle();
  const sidebarClasses = useStyleSidebar();

  const mainPanelClasses =
    sidebarClasses.mainPanel +
    " " +
    cx({
      [sidebarClasses.mainPanelSidebarMini]: miniActive,
      [sidebarClasses.mainPanelWithPerfectScrollbar]:
        navigator.platform.indexOf("Win") > -1,
    });

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const sidebarMinimize = () => {
    setMiniActive(!miniActive);
  };

  const getActiveRoute = (routes) => {
    let activeRoute = "교과서 관리";
    for (let i = 0; i < routes.length; i++) {
      if (routes[i].collapse) {
        let collapseActiveRoute = getActiveRoute(routes[i].views);
        if (collapseActiveRoute !== activeRoute) {
          return collapseActiveRoute;
        }
      } else {
        if (
          window.location.href.indexOf(routes[i].layout + routes[i].path) !== -1
        ) {
          return routes[i].name;
        }
      }
    }
    return activeRoute;
  };

  const getCategoryElements = (classification) => {
    return Axios.get("/api/v1/category/get/root", {
      withCredentials: true,
      params: {
        type: classification,
      },
    })
  };

  const flatArray = (arr, depth) => {
    if (arr === null || depth > 6) {
      return false;
    }

    for (let item of arr) {
      flattenArray.push({ id: item.id, name: item.name, depth: item.depth, parentId: item.parentId });
      if (item.child) {
        flatArray(item.children, depth + 1);
      }
    }
  }

  const changeEducationType = (type) => {
    getCategoryElements(type)
      .then((val) => {
        if (val.status === 200 && val.data.succeeded) {
          flatArray(val.data.data, 0);

          setFlattenArray(flattenArray);
        }
      })
      .catch((error) => {
        if (error.response.data.status === 403 && error.response.data.code === 'C006') {
          window.alert("세션이 만료되었습니다. 로그인 페이지로 이동합니다.");
          props.history ? props.history.push('/login') : window.location.href = '/login';
        }
      });
  };

  return (
    <div className={classes.container}>
      <Sidebar
        routes={routes}
        logoText={"CMS ADMIN"}
        image={image}
        handleDrawerToggle={handleDrawerToggle}
        open={mobileOpen}
        color={color}
        bgColor={bgColor}
        miniActive={miniActive}
        {...rest}
      />
      <div className={mainPanelClasses}>
        <AdminNavbar
          sidebarMinimize={sidebarMinimize.bind(this)}
          miniActive={miniActive}
          brandText={getActiveRoute(routes)}
          handleDrawerToggle={handleDrawerToggle}
          {...rest}
        />
        <form id="textbook_contents">
          <ContentsInputs
            new={isNew}
            changeEducationType={changeEducationType}
            basicInfo={basicInfo}
            flattenArray={flattenArray}
          />
        </form>
      </div>
    </div>
  );
}
