import React, { useState, useEffect } from "react";
import XS from 'xlsx';
import Axios from 'axios';

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Input from '@material-ui/core/Input';
import Collapse from '@material-ui/core/Collapse';

// core components
import CustomInput from 'components/CustomInput/CustomInput.js';
import Button from "components/CustomButtons/Button.js";
import Switch from "@material-ui/core/Switch";
import Radio from "@material-ui/core/Radio";
import FormControlLabel from "@material-ui/core/FormControlLabel";

import FiberManualRecord from "@material-ui/icons/FiberManualRecord";

import styles from "assets/jss/material-dashboard-pro-react/components/categoryContentStyle.js";
import { List, ListItem } from "@material-ui/core";

const useStyles = makeStyles(styles);

export default function CategoryContents(props) {
  const classes = useStyles();
  const [isEnabled, setStateEnable] = useState(false);
  const [showWindow, toggleStateofWindow] = useState(false);
  const [categoryPath, setCategoryPath] = useState('');
  const [selectedEnabled, setSelectedEnabled] = useState(0);

  useEffect(() => {
    setStateEnable(props.category.use);
    makePath(props.category.name, props.category.parentId);
    if (props.category.types === 'category') {
      setSelectedEnabled(0);
    } else if (props.category.types === 'top') {
      setSelectedEnabled(1);
    } else if (props.category.types === 'side') {
      setSelectedEnabled(2);
    }

  }, [props.category.use, props.category.id, props.category.types])

  const makePath = (path, categoryId) => {
    // final shape of return array is composed as seperated array along with 'depth'
    // regression search method

    if (categoryId === 1000) {
      setCategoryPath(path);
      return false;
    }

    Axios.get('/api/v1/category/get/listbyid', {
      withCredentials: true,
      params: {
        'category-id': categoryId
      }
    })
      .then((val) => {
        if (val.status === 200 && val.data.succeeded) {
          const updatedPath = val.data.data[0].name.concat(' > ' + path)
          makePath(updatedPath, val.data.data[0].parentId);
        }
      })
  }

  const updateCategoryAttr = (data) => {
    Axios.get('/api/v1/category/update', 
        { 
          withCredentials: true,
          params: {
            'category-id': data.categoryId ? data.categoryId : props.category.id,
            'is-use': data.use !== undefined ? data.use : props.category.useYn,
            'name': data.name ? data.name : props.category.name,
            'order': data.order ? data.order : props.category.order,
          }
        })
        .then((val) => {
          if (val.data.succeeded) {
            props.reRenderCategoryLists();
          }
        })
        .catch((error) => {
          if (error.response.data.status === 403 && error.response.data.code === 'C006') {
            window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');

            props.history ? props.history.push('/login') : window.location.href = '/login';
          }
        })
  }

  const setEnabled = (checked, categoryId) => {
    const dataset = {
      categoryId: categoryId,
      use: checked
    }

    updateCategoryAttr(dataset)
  }

  const addSubCategory = (dataset) => {
    const inputRef = document.getElementById("subCategoryInputs");
    const inputValue = inputRef.value;
    
    Axios.get('/api/v1/category/create', 
        { 
          withCredentials: true,
          params: dataset ? dataset : {
            'parent-id': props.category.id.toString(),
            'is-use': true,
            'type': props.category.types.toUpperCase(),
            'name': inputValue,
            'order': 0
          }
        })
        .then((val) => {
          if (val.data.succeeded) {
            props.reRenderCategoryLists();
            inputRef.value = "";
          }
        })
  }
  const editSubCategoryName = (categoryId, index) => {
    const confirmed = window.confirm("정말 수정하시겠습니까?");
    if (confirmed) {
      const replacedText = document.getElementById("replacedText_" + index).value;
      
      const dataset = {
        'category-id': categoryId,
        'name': replacedText,
      }

      updateCategoryAttr(dataset);
    }
  }

  const editCategoryType = (event) => {
    const categoryid = props.category.id;
    const type = event.target.value === '0' ? 'category' : event.target.value === '1' ? 'top' : 'side';

    if (type !== props.category.types) {
      const confirm = window.confirm(props.category.name + '의 타입을 ' + type + '으로 변경하시겠습니까?')
      if (confirm) {
        Axios.get('/api/v1/category/update/type', {
          params: {
            'category-id': categoryid,
            'type': type.toUpperCase()
          }
        })
          .then((res) => {
            if (res.status === 200) {
              props.reRenderCategoryLists();
            }
          })
          .catch((err) => {
            if (err.status === 400 && err.code === 'C001') {
              window.alert('변경 가능한 타입이 아닙니다.')
            }
          })
      }
      
    }
    
  }

  const deleteSubCategory = (categoryId) => {
    const confirmed = window.confirm("정말 삭제하시겠습니까?");
    if (confirmed) {
      Axios.get('/api/v1/category/delete', 
        { 
          withCredentials: true,
          params: {
            'category-id': categoryId
          }
        })
        .then((val) => {
          if (val.data.succeeded) {
            props.reRenderCategoryLists();
          }
        })
        .catch((error) => {
          if (error.response.data.status === 403 && error.response.data.code === 'C006') {
            window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');
            props.history ? props.history.push('/login') : window.location.href = '/login';
          }
        })
    }
  }
  const showWindowAboutRegistringEntireListsAtOnce = () => {
    toggleStateofWindow(!showWindow);
  }
  const updateFileState = (event) => {
    const f = event.target.files[0];
    
    const reader = new FileReader();
    reader.onload = (evt) => {
        /* Parse data */
        const bstr = evt.target.result;
        const wb = XS.read(bstr, {type:'binary'});
        /* Get first worksheet */
        const wsname = wb.SheetNames[0];
        const ws = wb.Sheets[wsname];
        /* Convert array of arrays */
        const datas = XS.utils.sheet_to_json(ws, {header:1});
        /* Update state */
        const confirmed = window.confirm("정말 일괄 등록 하시겠습니까?")
        if (confirmed) {
          for (let index=0; index < datas.length; index++) {
            // 첫줄 패스
            if (index === 0) {
              continue;
            } 
            const inputValue = datas[index][1];
            const subCategory = {
              'name': inputValue,
              'parent-id': props.category.id,
              'is-use': 1,
              'is-menu': 0,
              'is-reg-file': 0,
              'order': 0
            }
              
            addSubCategory(subCategory);
          }
          props.reRenderCategoryLists()
        }
    };
    reader.readAsBinaryString(f);
  }

  return (
    <div className={classes.contentContainer}>
      <div>
        <FormControlLabel
            control={
              <Switch
                checked={isEnabled}
                onChange={event => setEnabled(event.target.checked, props.category.id)}
                value="isEnabled"
                classes={{
                  switchBase: classes.switchBase,
                  checked: classes.switchChecked,
                  thumb: classes.switchIcon,
                  track: classes.switchBar
                }}
              />
            }
            classes={{
              label: classes.label
            }}
            label="활성화"
          />
          <FormControlLabel
            control={
              <Radio
                checked={selectedEnabled === 0}
                onChange={(ev) => editCategoryType(ev)}
                value={0}
                name="Category"
                aria-label="Category"
                icon={<FiberManualRecord className={classes.radioUnchecked} />}
                checkedIcon={
                  <FiberManualRecord className={classes.radioChecked} />
                }
                classes={{
                  checked: classes.radio,
                  root: classes.radioRoot
                }}
              />
            }
            classes={{
              label: classes.label
            }}
            label="Category"
          />
          <FormControlLabel
            control={
              <Radio
                checked={selectedEnabled === 1}
                onChange={(ev) => editCategoryType(ev)}
                value={1}
                name="Top"
                aria-label="Top"
                icon={<FiberManualRecord className={classes.radioUnchecked} />}
                checkedIcon={
                  <FiberManualRecord className={classes.radioChecked} />
                }
                classes={{
                  checked: classes.radio,
                  root: classes.radioRoot
                }}
              />
            }
            classes={{
              label: classes.label
            }}
            label="Top"
          />
          <FormControlLabel
            control={
              <Radio
                checked={selectedEnabled === 2}
                onChange={(ev) => editCategoryType(ev)}
                value={2}
                name="Side"
                aria-label="Side"
                icon={<FiberManualRecord className={classes.radioUnchecked} />}
                checkedIcon={
                  <FiberManualRecord className={classes.radioChecked} />
                }
                classes={{
                  checked: classes.radio,
                  root: classes.radioRoot
                }}
              />
            }
            classes={{
              label: classes.label
            }}
            label="Side"
          />
      </div>
        <h2 className={classes.contentHeader}>카테고리 경로</h2>
        <p className={classes.contents}>
            {categoryPath}
        </p>
        <h2 className={classes.contentHeader}>카테고리 아이디</h2>
        <p className={classes.contents}>
            {props.category.id}
        </p>
        <h2 className={classes.contentHeader}>하위 카테고리</h2>
        <Button color="facebook" onClick={() => showWindowAboutRegistringEntireListsAtOnce()}>일괄 등록</Button>
        <Collapse
          in={showWindow} timeout="auto" unmountOnExit
          >
          <List>
            <ListItem>
              <Input type="file" id="uploadedFile" onChange={(ev) => updateFileState(ev)}></Input>
            </ListItem>
          </List>
        </Collapse>
        <List>
        {
            props.category.child && props.category.children.length !== 0 ?
            props.category.children.map((value, index) => {
                return (
                    <ListItem key={index}>
                      <span className={classes.indexBedge}>{index}</span>  
                      <CustomInput 
                        id={"replacedText_" + index}
                        inputProps={{
                            placeholder: value.name
                        }}
                      />
                      <Button color="facebook" onClick={() => editSubCategoryName(value.id, index)}>수정</Button>
                      <Button color="danger" onClick={() => deleteSubCategory(value.id)}>삭제</Button>
                    </ListItem>
                )
            })
            :
            null
        }

            <ListItem>
                <CustomInput
                    id="subCategoryInputs"
                    inputProps={{
                        placeholder: "서브 카테고리 이름을 입력해주세요"
                    }}
                    formControlProps={{
                    fullWidth: true
                    }}
                />
                <Button color={"facebook"} onClick={() => addSubCategory()}>
                    추가
                </Button>
            </ListItem>
        </List>
        
    </div>
  );
}
