import React, { useCallback, useEffect, useState } from "react";
import Axios from "axios";

import { useDropzone } from "react-dropzone";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// core components
import Button from "components/CustomButtons/Button.js";

import Publish from "@material-ui/icons/Publish";
import SystemUpdateAltIcon from "@material-ui/icons/SystemUpdateAlt";

import SweetAlert from "react-bootstrap-sweetalert";

import styles from "assets/jss/material-dashboard-pro-react/components/fileManagerContentStyle.js";
import modalStyle from "assets/jss/material-dashboard-pro-react/modalStyle.js";

import {
  TableRow,
  TableCell,
  Table,
  Icon,
  Dialog,
  DialogContent,
  TextField,
  DialogActions,
  TableBody,
  TableHead
} from "@material-ui/core";

const useStyles = makeStyles(styles);
const useModalStyles = makeStyles(modalStyle);

export default function FileManagerContent(props) {
  const classes = useStyles();
  const modalClasses = useModalStyles();

  const [files, setFiles] = useState(null);
  const [uploadedFile, setUploadedFile] = useState(null);
  const [open, setOpen] = useState(false);
  const [editable, setEditable] = useState(false);
  const [selectedFile, setSelectedFile] = useState({});
  const [title, setTitle] = useState('');
  const [subtitle, setSubtitle] = useState('');
  const [alert, setAlert] = useState(null);

  const onDrop = useCallback((acceptedFiles) => {
    setUploadedFile(acceptedFiles);
    setOpen(true);
  }, []);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  useEffect(() => {
    getListMetaFiles()
  }, [props.category])

  const getListMetaFiles = () => {
    Axios.get("/api/v1/category/get/meta-files", {
      params: {
        "category-id": props.category.id,
      },
    }).then((res) => {
      setFiles(res.data.data);
    });
  }
  const successAlert = (type) => {
    const text = type === 'del' ? '성공적으로 삭제 되었습니다.' : '성공적으로 생성 되었습니다.'

    setAlert((
      <SweetAlert
        style={{ display: "block", marginTop: "-100px" }}
        title="Auto close alert!"
        onConfirm={() => this.hideAlert()}
        showConfirm={false}
      >
        {text}
      </SweetAlert>
    ))
    
    setTimeout(hideAlert, 2000);
  }

  const hideAlert = () => {
    setAlert(null)
    getListMetaFiles()
  }

  const getFilesTable = (files) => {
    if (files) {
      return files.map((val) => {
        return (
          <TableRow key={val.seq}>
            <TableCell>
              <p className={classes.title}>{val.title}</p>
              {
                val.subTitle ?
                <p className={classes.subTitle}>{val.subTitle}</p>
                :
                null
              }
            </TableCell>
            <TableCell>{val.fileName}</TableCell>
            <TableCell>
              <Button
              color={"facebook"}
              size={"sm"}
              onClick={() => {setOpen(true); setEditable(true); setSelectedFile(val)}} >
                수정
              </Button>
              <Button
              color={"danger"}
              size={"sm"}
              onClick={() => deleteFile(val.fileId)} >
                삭제
              </Button>
            </TableCell>
          </TableRow>
        );
      });
    }
    
  };
  
  const modifyMetadatas = (seq) => {
    if (seq && typeof seq === 'number') {
      const forms = new FormData();

      forms.append('seq', seq);
      forms.append('title', title.length !== 0 ? title : selectedFile.title);
      forms.append('sub-title', subtitle.length !== 0 ? subtitle : selectedFile.subTitle);
      forms.append('file', uploadedFile);

      Axios.post('/api/v1/category/file-update', forms)
        .then(res => {
          if (res.status === 200 && res.data.succeeded) {
            successAlert('create');
            setOpen(false);
            setEditable(false);
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }

  const deleteFile = (fileid) => {
    if (fileid && typeof fileid === 'string') {
      const dataset = {
        'category-id' : props.category.id,
        'file-id': fileid
      }
      const confirm = window.confirm('정말로 삭제하시겠습니까?');
      if (confirm) {
        Axios.get('/api/v1/category/remove', {
          params: dataset
        })
          .then(res => {
            if (res.status === 200) {
             successAlert('del');
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
      
    }
  }

  const transferFile = (ev) => {
    const forms = new FormData();
    
    forms.append('category-id', props.category.id);
    forms.append('file', uploadedFile[0]);
    forms.append('sub-title', subtitle);
    forms.append('title', title);

    Axios.post('/api/v1/category/file-upload', forms, {
      headers: {
        "content-type": "multipart/form-data",
      }
    })
      .then((res) => {
        if (res.status === 200 && res.data.succeeded) {
          successAlert();
          setOpen(false);
        }
      })
      .catch((err) => {
        if (err.status === 400 && err.code === 'C103') {
          window.alert('category type이 잘못되었습니다.')
        }
      })
  }

  return (
    <div className={classes.fileContent_wrap}>
      <h4>{props.category.name}</h4>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Title</TableCell>
            <TableCell>Filename</TableCell>
            <TableCell>Edit</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {getFilesTable(files)}
        </TableBody>
      </Table>

      <div {...getRootProps()}>
        <input {...getInputProps()} />
        {isDragActive ? (
          <div className={classes.drop_zone + " " + classes.active}>
            <Icon>
              <Publish fontSize={"large"} />
              <p className={classes.drop_zone_description}>Drop Here!</p>
            </Icon>
          </div>
        ) : (
          <div className={classes.drop_zone}>
            <Icon fontSize={"large"}>
              <SystemUpdateAltIcon />
            </Icon>
            <p className={classes.drop_zone_description}>
              Choose File and Drop here
            </p>
          </div>
        )}
      </div>

      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        className={modalClasses.modal}
      >
        <DialogContent className={classes.modalBody}>
          <p className={modalClasses.modalTitle}>파일 제목을 입력하세요.</p>
          <TextField
            id="filetitle"
            onChange={(ev) => setTitle(ev.target.value)}
            className={classes.form}
            
          ></TextField>
          <p className={modalClasses.modalTitle}>파일 부제목을 입력하세요</p>
          <TextField
            id="filesubtitle"
            onChange={(ev) => setSubtitle(ev.target.value)}
            className={classes.form}
          ></TextField>
          <p className={modalClasses.modalTitle}>변경할 파일을 올려주세요</p>
          <input type="file" onChange={(ev) => setUploadedFile(ev.target.files[0])}></input>
        </DialogContent>
        <DialogActions
          className={
            modalClasses.modalFooter + " " + modalClasses.modalFooterCenter
          }
        >
          <Button
            color={"danger"}
            size={"sm"}
            onClick={() => setOpen(false)}
          >
            취소
          </Button>
          <Button
            color={"facebook"}
            size={"sm"}
            onClick={editable ? () => modifyMetadatas(selectedFile.seq) : (ev) => transferFile(ev)}
          >
            확인
          </Button>
        </DialogActions>
      </Dialog>

      {alert}
    </div>
  );
}
