import React from "react";
import Axios from 'axios';

import {makeStyles} from '@material-ui/core/styles'
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import Paginations from "components/Pagination/Pagination.js";
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    marginTop: '50px'
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
}));

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}

function SortableHeader(props) {
  const headers = [
    {id: 'unitCode', label: 'no.'},
    {id: 'level', label: '학교급'},
    {id: 'textbookName', label: '교과서명'},
    {id: 'author', label: '저자'},
  ];

  const {orderBy, order, handleOrderBy} = props;
  const classes = useStyles();

  const createSortHandler = (id) => {
    handleOrderBy( order === 'asc' ? 'desc' : 'asc', id)  
  }
  
  return (
    <TableHead>
        <TableRow>
          {headers.map((headCell, index) => (
            <TableCell
              key={headCell.id}
              align={'left'}
              sortDirection={orderBy === headCell.id ? order : false}
            >
              <TableSortLabel
                active={orderBy === headCell.id}
                direction={orderBy === headCell.id ? order : 'asc'}
                onClick={() => createSortHandler(headCell.id)}
              >
                {headCell.label}
                {orderBy === headCell.id ? (
                  <span className={classes.visuallyHidden}>
                    {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                  </span>
                ) : null}
              </TableSortLabel>
            </TableCell>
          ))}
        </TableRow>
      </TableHead>
  )
}

export default class ContentListTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      contentLists: [],
      order: 'desc',
      orderBy: 'unitCode',
      page: 0,
      totalpage: 0,
      curNavication: 1
    };
  }

  componentDidMount() {
    
    Axios.get('/api/v1/book/get/filter-list', {
      params: {
        gradeCids: this.props.filterState.level,
        title: this.props.filterState.text
      }
    })
      .then((val) => {
        if (val.status === 200 && val.data.succeeded) {
          this.setState({
            contentLists : val.data.data,
            page: val.data.page,
            totalpage: val.data.totalPage
          })
        }
      })
      .catch((error) => {
        if (error.response.data.status === 403 && error.response.data.code === 'C006') {
          window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');
          this.props.history ? this.props.history.push('/login') : window.location.href = '/login';
        }
      })
  }
  componentWillReceiveProps(nextprops) {
    if (this.props.allContentList !== nextprops.allContentList) {
      this.setState({
        contentLists: nextprops.allContentList.data,
        page: nextprops.allContentList.page,
        totalpage: nextprops.allContentList.totalPage
      })
    }
  }

  handleOrderBy = (order, orderBy) => {
    this.setState({order: order, orderBy: orderBy})
  }

  generatePages = (totalPage) => {
    const arr = [{text: 'PREV', onClick: () => this.transferListPage(this.state.page - 1)}];

    for (var i=0; i < totalPage; i++) {
      arr.push({
        text: i + 1,
        active: this.state.curNavication - 1 === i ? true : false,
        onClick: (event) => { this.transferListPage(parseInt(event.target.innerText)); this.setState({curNavication: parseInt(event.target.innerText)}) }
      })
      if (i === totalPage - 1) {
        arr.push({
          text:"NEXT",
          onClick: () => this.transferListPage(this.state.page + 1)
        })
      }
    }

    return arr;
  }

  transferListPage = (page) => {
    const dataset = {
      gradeCids: this.props.filterState.level === 0 ? null : this.props.filterState.level,
      revisionCids: this.props.filterState.curriculum === 0 ? null : this.props.filterState.curriculum,
      title: this.props.filterState.text.length === 0 ? null : this.props.filterState.text
    }

    Axios.get('/api/v1/book/get/filter-list', {
      params: {
        page: page,
        perPage: 10,
        ...dataset
      }
    })
      .then((val) => {
        if (val.status === 200 && val.data.succeeded) {
          this.setState({
            contentLists : val.data.data,
            page: val.data.page,
            totalpage: val.data.totalPage
          })
        }
      })
      .catch((error) => {
        if (error.response.data.status === 403 && error.response.data.code === 'C006') {
          window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');
          this.props.history ? this.props.history.push('/login') : window.location.href = '/login';
        }
      })
  }

  render() {
    const {order, orderBy, contentLists} = this.state;
    
    return (
      <div>
        <TableContainer>
          <Table>
            <SortableHeader order={order} orderBy={orderBy} handleOrderBy={this.handleOrderBy.bind(this)}></SortableHeader>
            <TableBody>
              {
                stableSort(contentLists, getComparator(order, orderBy))
                  .map((val, idx) => {
                    return (
                      <TableRow key={idx}>
                        <TableCell>
                          {idx}
                        </TableCell>
                        <TableCell>
                          {val.gradeName}
                        </TableCell>
                        <TableCell>
                          <a href={this.props.linkUrl + '?new=false&seq=' + val.seq + '&bookCid=' + val.bookCid}>
                            {val.titleName}
                          </a>
                        </TableCell>
                        <TableCell>
                          {val.maker}
                        </TableCell>
                      </TableRow>
                    )
                  })
              }
            </TableBody>
          </Table>  
        </TableContainer>
        <Paginations 
          pages={this.generatePages(this.state.totalpage)}
          color="info"
        />
      </div>
      
    )
  }
}
