import React from "react";
import Axios from 'axios';

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";

// @material-ui/icons
import Email from "@material-ui/icons/Email";
// import LockOutline from "@material-ui/icons/LockOutline";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";

import styles from "assets/jss/material-dashboard-pro-react/views/loginPageStyle.js";

const useStyles = makeStyles(styles);

export default function LoginPage(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  React.useEffect(() => {
    let id = setTimeout(function () {
      setCardAnimation("");
    }, 30);
    // Specify how to clean up after this effect:
    return function cleanup() {
      window.clearTimeout(id);
    };
  }, []);
  const classes = useStyles();
  const processLogin = (event) => {
    const params = {
      id: document.forms[0]['identification'].value,
      password: document.forms[0]['password'].value
    }
    const requestURL = `/api/v1/member/login?password=${params.password}&userid=${params.id}`
    Axios.get(requestURL, {
      withCredentials: true
    })
      .then((val) => {
        if (val.status === 200 && val.data.succeeded) {
          
          function setCookie(cname, cvalue, exTimes) {
            var d = new Date();
            d.setTime(d.getTime() + (exTimes*60*60*1000));
            var expires = "expires="+ d.toUTCString();
            document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
          }
          const userInfos = {
            name: val.data.data[0].userName
          }
          setCookie('JSESSIONID', val.data.data[0].jsessionid, 2);
          setCookie('TICKET', val.data.data[0].passwordTicket, 2);
          setCookie('USERSIMPLEINFOS', JSON.stringify(userInfos), 2);
          props.history.push('/')
        }
      })
      .catch((error) => {
        if (error.response.data.status === 403 && error.response.data.code === 'C006') {
          window.alert('세션이 만료되었습니다. 로그인 페이지로 이동합니다.');
          props.history ? props.history.push('/login') : window.location.href = '/login';
        }
      })
  }
  return (
    <div className={classes.container}>
      <GridContainer justify="center">
        <GridItem xs={12} sm={6} md={4}>
          <form>
            <Card login className={classes[cardAnimaton]}>
              <CardHeader
                className={`${classes.cardHeader} ${classes.textCenter}`}
                color="info"
              >
                <h4 className={classes.cardTitle}>Log in</h4>
              </CardHeader>
              <CardBody>
                <CustomInput
                  labelText="ID"
                  id="identification"
                  formControlProps={{
                    fullWidth: true,
                  }}
                  inputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <Email className={classes.inputAdornmentIcon} />
                      </InputAdornment>
                    ),
                  }}
                />
                <CustomInput
                  labelText="Password"
                  id="password"
                  formControlProps={{
                    fullWidth: true,
                  }}
                  inputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <Icon className={classes.inputAdornmentIcon}>
                          lock_outline
                        </Icon>
                      </InputAdornment>
                    ),
                    type: "password",
                    autoComplete: "off",
                  }}
                />
              </CardBody>
              <CardFooter className={classes.justifyContentCenter}>
                <Button color="info" simple size="lg" block onClick={(ev) => processLogin(ev)}>
                  SIGN IN
                </Button>
              </CardFooter>
            </Card>
          </form>
        </GridItem>
      </GridContainer>
    </div>
  );
}
