import React, { useState, useEffect } from "react";
import Axios from "axios";
import { makeStyles } from "@material-ui/core/styles";

import Dialog from "@material-ui/core/Dialog";
import { DialogActions } from "@material-ui/core";
import DialogContent from "@material-ui/core/DialogContent";

import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Button from "components/CustomButtons/Button.js";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableRow from "@material-ui/core/TableRow";

const useModalStyles = makeStyles((theme) => ({
  modal: {
    [theme.breakpoints.up("sm")]: {
      maxWidth: "1000px",
      margin: "auto",
    },
    borderRadius: "6px",
    marginTop: "100px !important",
    overflow: "visible",
    maxHeight: "unset",
    position: "relative",
    height: "fit-content",
  },
  modalBody: {
    paddingTop: "14px",
    paddingRight: "14px",
    paddingBottom: "16px",
    paddingLeft: "24px",
    position: "relative",
    overflow: "visible",
    width: "600px",
  },
  modalFooter: {
    padding: "15px",
    textAlign: "right",
    paddingTop: "0",
    margin: "0",
    justifyContent: "center",
  },
  modalFooterCenter: {
    marginLeft: "auto",
    marginRight: "auto",
  },
}));

export default function DialogExposeCategory(props) {
  const [level, setLevel] = useState("");
  const [grade, setGrade] = useState("");
  const [subject, setSubject] = useState("");
  const [textbookName, setTextbookName] = useState("");
  const [levels, setlevels] = useState([]);
  const [subjects, setSubjects] = useState([]);
  const [textbook, setTextbook] = useState([]);
  
  const [flattenArray, setFlattenArray] = useState([]);

  const modalClasses = useModalStyles();

  const handleChange = (event) => {
    const name = event.target.name;
   
   if (name === "grade") {
      getCategoryListbyid(event.target.value, "sub");
      setLevel(event.target.value);
    } else if (name === "subject") {
      getCategoryListbyid(event.target.value, "textbook");
      setSubject(event.target.value);
    }
  };

  const getCategoryListbyid = (categoryId, type) => {
    Axios.get("/api/v1/category/get/listbyid", {
      withCredentials: true,
      params: {
        "category-id": categoryId,
      },
    }).then((result) => {
      if (result.status === 200 && result.data.succeeded) {
        let arr = [];

        result.data.data[0].children.forEach((val) => {
          arr.push({ id: val.id, name: val.name });
        });
        if (type === "grade") {
          setlevels(arr)

          if (arr.length !== 0) {
            getCategoryListbyid(arr[0].id, "sub");
          }
        } else if (type === "sub") {
          setSubjects(arr);

          if (arr.length !== 0) {
            getCategoryListbyid(arr[0].id, "textbook");
          }
        } else if (type === "textbook") {
          setTextbook(arr);
          setTextbookName(arr[0].id)
        }
      }
    });
  };

  const getCategoryElements = (classification) => {
    return Axios.get("/api/v1/category/get/root", {
      withCredentials: true,
      params: {
        type: classification,
      },
    });
  };

  const flatArray = (arr, depth) => {
    if (arr === null || depth > 5) {
      return false;
    }

    for (let item of arr) {
      flattenArray.push({
        id: item.id,
        name: item.name,
        depth: item.depth,
        parentId: item.parentId,
      });
      if (item.child) {
        flatArray(item.children, depth + 1);
      }
    }
  };

  const changeEducationType = (event) => {
    const type = event.target.value === 1001 ? "E" : "M";

    getCategoryElements(type)
      .then((val) => {
        if (val.status === 200 && val.data.succeeded) {
          flatArray(val.data.data, 0);

          setFlattenArray(flattenArray);
          setGrade(event.target.value);
        }
      })
      .catch((error) => {
        if (
          error.response.data.status === 403 &&
          error.response.data.code === "C006"
        ) {
          window.alert("세션이 만료되었습니다. 로그인 페이지로 이동합니다.");
          props.history
            ? props.history.push("/login")
            : (window.location.href = "/login");
        }
      });
  };

  const submitExposeCategory = () => {
    const prevStatement = props.exposeCategories;
    const dataset = {
        level: level,
        grade: grade,
        subject: subject,
        textbookName: textbookName
    }
    props.updateState({
      exposeCategories: [[...prevStatement, dataset]],
    });
  };

  return (
    <Dialog
      open={props.open}
      onClose={props.showCategorySelectPopup}
      className={modalClasses.modal}
    >
      <DialogContent className={modalClasses.modalBody}>
        <TableContainer>
          <Table>
            <TableBody>
              <TableRow>
                <TableCell>학교급</TableCell>
                <TableCell>
                  <FormControl>
                    <Select
                      native
                      value={grade}
                      onChange={changeEducationType}
                      inputProps={{
                        name: "grade",
                        id: "grade",
                      }}
                    >
                      <option value={1001}>초등</option>
                      <option value={1002}>중고등</option>
                    </Select>
                  </FormControl>
                </TableCell>
                <TableCell>학년</TableCell>
                <TableCell>
                  <FormControl>
                    <Select
                      native
                      value={level}
                      onChange={handleChange}
                      inputProps={{
                        name: "level",
                        id: "level",
                      }}
                    >
                      {levels
                        ? levels.map((val, index) => {
                            return (
                              <option value={val.id} key={index}>
                                {val.name}
                              </option>
                            );
                          })
                        : null}
                    </Select>
                  </FormControl>
                </TableCell>
              </TableRow>

              <TableRow>
                
                <TableCell>과목</TableCell>
                <TableCell>
                  <FormControl>
                    <Select
                      native
                      value={subject}
                      onChange={handleChange}
                      inputProps={{
                        name: "subject",
                        id: "subject",
                      }}
                    >
                      {subjects
                        ? subjects.map((val, index) => {
                            return (
                              <option value={val.id} key={index}>
                                {val.name}
                              </option>
                            );
                          })
                        : null}
                    </Select>
                  </FormControl>
                </TableCell>
                <TableCell>교과서명</TableCell>
                <TableCell>
                  <FormControl>
                    <Select
                      native
                      value={textbookName}
                      onChange={handleChange}
                      inputProps={{
                        name: "textbook",
                        id: "textbook",
                      }}
                    >
                      {textbook
                        ? textbook.map((val, index) => {
                            return (
                              <option value={val.id} key={index}>
                                {val.name}
                              </option>
                            );
                          })
                        : null}
                    </Select>
                  </FormControl>
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
      </DialogContent>
      <DialogActions
        className={
          modalClasses.modalFooter + " " + modalClasses.modalFooterCenter
        }
      >
        <Button
          color={"danger"}
          size={"sm"}
          onClick={() => props.showCategorySelectPopup(false)}
        >
          취소
        </Button>
        <Button color={"facebook"} size={"sm"} onClick={submitExposeCategory}>
          확인
        </Button>
      </DialogActions>
    </Dialog>
  );
}
